'use client'

import { useState, useEffect } from 'react'
import { useRouter } from 'next/navigation'
import { Card, CardContent, CardHeader, CardTitle } from '@/components/ui/card'
import { Button } from '@/components/ui/button'
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from '@/components/ui/select'
import { Dialog, DialogContent, DialogHeader, DialogTitle, DialogDescription } from '@/components/ui/dialog'

export default function DateTimeSelectionPage() {
  const [selectedDate, setSelectedDate] = useState<string>('')
  const [selectedTime, setSelectedTime] = useState<string>('')
  const [selectedDuration, setSelectedDuration] = useState<string>('')
  const [selectedPlace, setSelectedPlace] = useState<string>('')
  const [selectedRoom, setSelectedRoom] = useState<string>('')
  const router = useRouter()

  useEffect(() => {
    // Get the selected place and room from localStorage
    const place = localStorage.getItem('selectedPlace')
    const room = localStorage.getItem('selectedRoom')
    
    if (!place || !room) {
      router.push('/place-selection')
      return
    }
    
    setSelectedPlace(place)
    setSelectedRoom(room)
  }, [router])

  // Generate next 30 days
  const generateDates = () => {
    const dates = []
    const today = new Date()
    
    for (let i = 0; i < 30; i++) {
      const date = new Date(today)
      date.setDate(today.getDate() + i)
      
      const persianDate = date.toLocaleDateString('fa-IR', {
        year: 'numeric',
        month: 'long',
        day: 'numeric',
        weekday: 'long'
      })
      
      const isoString = date.toISOString().split('T')[0]
      
      dates.push({
        date: isoString,
        persianDate: persianDate,
        dayOfWeek: date.toLocaleDateString('fa-IR', { weekday: 'long' }),
        day: date.getDate(),
        isToday: i === 0
      })
    }
    
    return dates
  }

  // Generate time slots (9 AM to 6 PM in 30-minute intervals)
  const generateTimeSlots = () => {
    const slots = []
    for (let hour = 9; hour <= 17; hour++) {
      for (let minute of [0, 30]) {
        if (hour === 17 && minute === 30) break // Don't include 5:30 PM
        
        const time24 = `${hour.toString().padStart(2, '0')}:${minute.toString().padStart(2, '0')}`
        const persianTime = `${hour}:${minute === 0 ? '00' : '30'}`
        
        slots.push({
          value: time24,
          label: persianTime
        })
      }
    }
    return slots
  }

  const durations = [
    { value: '0.5', label: '۳۰ دقیقه' },
    { value: '1', label: '۱ ساعت' },
    { value: '1.5', label: '۱.۵ ساعت' },
    { value: '2', label: '۲ ساعت' },
    { value: '2.5', label: '۲.۵ ساعت' },
    { value: '3', label: '۳ ساعت' },
    { value: '4', label: '۴ ساعت' },
    { value: '5', label: '۵ ساعت' },
    { value: '6', label: '۶ ساعت' },
    { value: '8', label: '۸ ساعت' }
  ]

  const handleContinue = () => {
    if (!selectedDate || !selectedTime || !selectedDuration) {
      alert('لطفا تاریخ، زمان و مدت رزرو را انتخاب کنید')
      return
    }
    
    // Calculate end time
    const [hours, minutes] = selectedTime.split(':').map(Number)
    const durationHours = parseFloat(selectedDuration)
    const totalMinutes = hours * 60 + minutes + (durationHours * 60)
    const endHours = Math.floor(totalMinutes / 60)
    const endMinutes = totalMinutes % 60
    const endTime = `${endHours}:${endMinutes.toString().padStart(2, '0')}`
    
    // Store all data
    localStorage.setItem('selectedDate', selectedDate)
    localStorage.setItem('selectedTime', selectedTime)
    localStorage.setItem('selectedDuration', selectedDuration)
    localStorage.setItem('endTime', endTime)
    
    console.log('Storing reservation data:', {
      date: selectedDate,
      time: selectedTime,
      duration: selectedDuration,
      endTime: endTime,
      place: selectedPlace,
      room: selectedRoom
    })
    
    router.push('/success')
  }

  const getPlaceName = () => {
    return selectedPlace === 'planet1' ? 'پلنت ۱' : 'پلنت ۲'
  }

  const getRoomName = () => {
    const roomNames = {
      'meeting-room': 'اتاق جلسه',
      'phone-booth': 'باکس تماس تلفنی',
      'amphitheater': 'آمفی تئاتر'
    }
    return roomNames[selectedRoom as keyof typeof roomNames] || selectedRoom
  }

  const calculateEndTime = () => {
    if (!selectedTime || !selectedDuration) return ''
    
    const [hours, minutes] = selectedTime.split(':').map(Number)
    const durationHours = parseFloat(selectedDuration)
    const totalMinutes = hours * 60 + minutes + (durationHours * 60)
    
    const endHours = Math.floor(totalMinutes / 60)
    const endMinutes = totalMinutes % 60
    
    return `${endHours}:${endMinutes.toString().padStart(2, '0')}`
  }

  const dates = generateDates()
  const timeSlots = generateTimeSlots()

  return (
    <div className="min-h-screen bg-gradient-to-br from-gray-50 to-blue-50 p-4">
      {/* Header */}
      <div className="max-w-6xl mx-auto">
        <div className="text-center mb-8 pt-8">
          <div className="w-16 h-16 mx-auto bg-primary-500 rounded-xl flex items-center justify-center mb-4">
            <div className="w-10 h-10 bg-white rounded-lg flex items-center justify-center">
              <div className="text-primary-500 text-xl font-bold">P</div>
            </div>
          </div>
          <h1 className="text-3xl font-bold text-gray-800 mb-2">سیستم رزرو خدمات پلنت</h1>
          <div className="text-gray-600 space-y-1">
            <p>مکان: <span className="font-semibold text-primary-600">{getPlaceName()}</span></p>
            <p>نوع اتاق: <span className="font-semibold text-primary-600">{getRoomName()}</span></p>
          </div>
        </div>

        <div className="grid grid-cols-1 lg:grid-cols-2 gap-8 max-w-6xl mx-auto">
          {/* Date Selection */}
          <Card>
            <CardHeader>
              <CardTitle className="text-center text-xl">تاریخ رزرو</CardTitle>
            </CardHeader>
            <CardContent>
              <div className="grid grid-cols-2 md:grid-cols-3 gap-3 max-h-96 overflow-y-auto">
                {dates.map((dateObj) => (
                  <div
                    key={dateObj.date}
                    className={`p-3 rounded-lg border-2 cursor-pointer transition-all duration-200 hover:shadow-md text-center ${
                      selectedDate === dateObj.date
                        ? 'border-primary-500 bg-primary-50 shadow-lg'
                        : dateObj.isToday
                        ? 'border-primary-300 bg-primary-25 hover:border-primary-400'
                        : 'border-gray-200 bg-white hover:border-primary-200'
                    }`}
                    onClick={() => setSelectedDate(dateObj.date)}
                  >
                    <div className={`text-xl font-bold mb-1 ${
                      selectedDate === dateObj.date ? 'text-primary-600' : 
                      dateObj.isToday ? 'text-primary-500' : 'text-gray-700'
                    }`}>
                      {dateObj.day}
                    </div>
                    <div className={`text-xs mb-1 ${
                      selectedDate === dateObj.date ? 'text-primary-600' : 'text-gray-600'
                    }`}>
                      {dateObj.dayOfWeek}
                    </div>
                    {dateObj.isToday && (
                      <div className="text-xs text-primary-500 font-medium">امروز</div>
                    )}
                    {selectedDate === dateObj.date && (
                      <div className="mt-2">
                        <div className="w-3 h-3 bg-primary-500 rounded-full mx-auto"></div>
                      </div>
                    )}
                  </div>
                ))}
              </div>
            </CardContent>
          </Card>

          {/* Time and Duration Selection */}
          <Card>
            <CardHeader>
              <CardTitle className="text-center text-xl">زمان و مدت رزرو</CardTitle>
            </CardHeader>
            <CardContent className="space-y-6">
              {/* Start Time */}
              <div className="space-y-2">
                <label className="block text-sm font-medium text-gray-700">زمان شروع</label>
                <Select value={selectedTime} onValueChange={setSelectedTime}>
                  <SelectTrigger className="h-12">
                    <SelectValue placeholder="زمان شروع را انتخاب کنید" />
                  </SelectTrigger>
                  <SelectContent>
                    {timeSlots.map((slot) => (
                      <SelectItem key={slot.value} value={slot.value}>
                        {slot.label}
                      </SelectItem>
                    ))}
                  </SelectContent>
                </Select>
              </div>

              {/* Duration */}
              <div className="space-y-2">
                <label className="block text-sm font-medium text-gray-700">مدت زمان رزرو</label>
                <Select value={selectedDuration} onValueChange={setSelectedDuration}>
                  <SelectTrigger className="h-12">
                    <SelectValue placeholder="مدت زمان رزرو را انتخاب کنید" />
                  </SelectTrigger>
                  <SelectContent>
                    {durations.map((duration) => (
                      <SelectItem key={duration.value} value={duration.value}>
                        {duration.label}
                      </SelectItem>
                    ))}
                  </SelectContent>
                </Select>
              </div>

              {/* Summary */}
              {selectedTime && selectedDuration && (
                <div className="bg-primary-50 border border-primary-200 rounded-lg p-4 mt-6">
                  <h3 className="font-medium text-primary-800 mb-2">خلاصه رزرو:</h3>
                  <div className="text-sm text-primary-700 space-y-1">
                    <p>زمان شروع: <span className="font-medium">{selectedTime}</span></p>
                    <p>زمان پایان: <span className="font-medium">{calculateEndTime()}</span></p>
                    <p>مدت زمان: <span className="font-medium">{durations.find(d => d.value === selectedDuration)?.label}</span></p>
                  </div>
                </div>
              )}
            </CardContent>
          </Card>
        </div>

        {/* Continue Button */}
        <div className="text-center mt-8">
          <Button 
            onClick={handleContinue}
            className="w-full max-w-md h-12 bg-primary-500 hover:bg-primary-600 text-white font-medium text-base"
            disabled={!selectedDate || !selectedTime || !selectedDuration}
          >
            تایید و ثبت رزرو
          </Button>
        </div>
      </div>
    </div>
  )
}
